import React, {Component} from 'react';
import Axios from 'axios';
import Alert from 'react-s-alert';
import './status.css';

class Status extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sendCD: 0,
    };
    this.statusChinese = {
      unconfirmed: '',
      confirmed: '',
      reviewing: '',
      reviewed: '',
      admin: ''
    };
    this.statusInfo = {
      unconfirmed: (
        <div>
          <p></p>
          <button disabled={this.state.sendCD > 0} onClick={(e) => this.resendConfirmation()}></button>
          {
            this.state.sendCD > 0 ? (<p className="send-cd">{this.state.sendCD}</p>) : null
          }
        </div>
      ),
      confirmed: (
        <div>
          <p></p>
          <button onClick={(e) => this.handleConfirm()}></button>
        </div>
      ),
      reviewing: (
        <div>
          <p></p>
        </div>
      ),
      reviewed: (
        <div>
          <p></p>
        </div>
      ),
      admin: (
        <div>
          <p></p>
        </div>
      )
    };
  }
  handleConfirm() {
    Axios({
      method: 'put',
      headers: {
        Authorization: this.props.auth,
      },
      url: '/api/profile'
    }).then((res) => {
      Alert.success('');
      this.props.onUpdate();
    }).catch((err) => {
      this.props.onError(err);
    });
  }
  resendConfirmation() {
    Axios({
      method: 'post',
      headers: {
        Authorization: this.props.auth,
      },
      url: `/api/user/${this.props.id}/confirmation`
    }).then((res) => {
      Alert.success('');
      this.props.onUpdate();
    }).catch((err) => {
      this.props.onError(err);
    });
  }

  render() {
    return (
      <div className="status">
        <p className="status-title">
          {this.statusChinese[this.props.status]}
        </p>
        <div className="status-info">
          {this.statusInfo[this.props.status]}
        </div>
      </div>
    );
  }
}

Status.propTypes = {
  id: React.PropTypes.string,
  status: React.PropTypes.oneOf([
    'unconfirmed',
    'confirmed',
    'reviewing',
    'reviewed',
    'admin'
  ]),
  auth: React.PropTypes.string,
  history: React.PropTypes.object,
  onUpdate: React.PropTypes.func,
  onError: React.PropTypes.func,
  readOnly: React.PropTypes.bool
};

export default Status;



